import { Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild } from '@angular/core';
import { IUnidadDeMedida } from '../models/unidad-de-medida';
import { UnidadesDeMedidaService } from '../service/unidades-de-medida.service';
import { Subject, Subscription, debounceTime, distinctUntilChanged, firstValueFrom, takeUntil } from 'rxjs';
import { MatTableDataSource } from '@angular/material/table'
import { SaveComponent } from './save/save.component';
import { FuseConfirmationService } from '@fuse/services/confirmation';
import { AppAlertService } from '@shared/service/app/alert.service';
import { FormControl } from '@angular/forms';
import { TablaDinamicaComponent } from '@shared/components/tabla-dinamica/tabla-dinamica.component';
import { MatDialog } from '@angular/material/dialog';
import { environment } from 'environments/environment';
import { SearchService } from 'app/layout/common/search/search.service';
import { MatButton } from '@angular/material/button';
import { JedsMenuService } from '@shared/service/app/jeds-menu.service';
import { FilterMenuComponent } from './filter-menu/filter-menu.component';
import { IFilter } from '@shared/models/filter';
import { HeaderTextService } from '@shared/components/header-text/header-text.service';
import { Router } from '@angular/router';
import { EmpresaService } from '@shared/service/app/empresa.service';
@Component({
    selector: 'app-unidades-de-medida',
    templateUrl: './unidades-de-medida.component.html',
    styleUrls: ['./unidades-de-medida.component.scss']
})
export class UnidadesDeMedidaComponent implements OnInit, OnDestroy {
    pedidos_de_presupuesto: IUnidadDeMedida[];
    public dataSource: MatTableDataSource<IUnidadDeMedida> = new MatTableDataSource();
    headers = ['Descripcion', 'Activo', 'Acciones'];
    column_params = ['descripcion', 'activo', 'acciones'];
    funcionesObjeto = null;
    filtroBusqueda: string = '';
    filtersLike = ['descripcion'];
    filter: IFilter = {
        activo: -1,
    };
    @Input() extraParams = '';
    @Output() parametrosActualizados = new EventEmitter<any>();
    env = environment.stock.unidad_medidas + '?sort=descripcion'; 
    search: FormControl = new FormControl('');
    sortValue: string;
    FilterEventSub$: Subscription;
    SearchTextSub$: Subscription;
    color_primario: string;
    color_secundario: string;
    default_color: string = 'border-primary text-primary';
    private _unsubscribeAll: Subject<any> = new Subject<any>();

    constructor(
        private _unidadesDeMedidaService: UnidadesDeMedidaService,
        private _empresaService: EmpresaService,
        private confirm: FuseConfirmationService,
        private alert: AppAlertService,
        private dialogo: MatDialog,
        private _searchService: SearchService,
        private _menuService: JedsMenuService,
        public _headerTextService: HeaderTextService,
        private router: Router,

    ) {
        this._headerTextService.setTitulo(this.router);

        /**
         * Aca se declaran los botones que iran en la grilla sobre el apartado de acciones.
         */
        this.funcionesObjeto = [
            {
                nombre_boton: "Editar",
                functionName: 'editar',
                iconoAccion: (um: IUnidadDeMedida) => 'jedstion:editar',
                iconoAccionado: (um: IUnidadDeMedida) => '',
                iconoDeshabilitado: (um: IUnidadDeMedida) => '',
                visible: (um: IUnidadDeMedida) => true
            },
            {
                nombre_boton: "Eliminar",
                functionName: 'eliminar',
                iconoAccion: (um: IUnidadDeMedida) => 'jedstion:eliminar',
                iconoAccionado: (um: IUnidadDeMedida) => '',
                iconoDeshabilitado: (um: IUnidadDeMedida) => '',
                visible: (um: IUnidadDeMedida) => true
            },
        ];
    }

    @ViewChild(TablaDinamicaComponent) tabla: TablaDinamicaComponent;
    ngOnInit(): void {
        // Subscribe to empresa data
        this._empresaService.empresa$
            .pipe(takeUntil(this._unsubscribeAll))
            .subscribe(empresa => {
                this.color_primario = empresa.color_primario;
                this.color_secundario = empresa.color_secundario;
            });
        
        // Load empresa data
        this._empresaService.getEmpresa();
        this.SearchTextSub$ = this._searchService.getSearchText().pipe( //Se obtienen los valores que coinciden con lo ingresado en la barra de busqueda
            debounceTime(250),
            distinctUntilChanged()
        )
            .subscribe(value => {
                this.filtroBusqueda = value;
                this.tabla.filters(this.filtroBusqueda);
            });

        this.FilterEventSub$ = this._searchService.getFilterEvent().subscribe(event => {
            this.onFilterMenu(this._searchService.getFilterOrigin())
        });
    }

    /**
     * Esta funcion se encarga de levantar los datos que se van a filtrar en el componente general del filtro
     * @param origin 
     */
    onFilterMenu(origin: MatButton) {
        this._menuService
            .open(FilterMenuComponent, origin._elementRef, {
                filter: this.filter,
            })
            .afterClosed()
            .subscribe({
                next: filter => {
                    if (filter) {
                        this.filter = filter;
                        this.extraParams = this.filter.activo != -1 ? "filter[activo]=" + this.filter.activo : '';
                        this.tabla.filters(this.filtroBusqueda);
                        // this._searchService.getSearchInput().setValue(this._tabsServices.getFiltroBuscadorPedido());

                    }
                },
            });
    }

    /**
 * Esta es la funcion que se encarga de la columna acciones dentro de la grilla dinamica
 * @param event 
 */
    generarAcciones(event: { name: string, params: any }) {
        switch (event.name) {
            case 'editar':
                this.updateUnidadDeMedida(event);
                break;
            case 'eliminar':
                this.deleteUnidadDeMedida(event);
                break;

        }
    }

    /**
     * Esta funcion guarda un item
     */
    saveUnidadDeMedida() {
        this.dialogo
            .open(SaveComponent, { panelClass: 'ayuda-dialog' })
            .componentInstance.creadoExitosamente
            .subscribe({
                next: () => {
                    this.alert.success("Unidad de medida creada con éxito.");
                    this.tabla.filters(this.filtroBusqueda);
                },
            });
    }

    /**
     * Esta funcion modifica un item
     * @param event 
     */
    updateUnidadDeMedida(event) {
        this.dialogo
            .open(SaveComponent, { data: event.params.data, panelClass: 'my-dialog', autoFocus: false })
            .componentInstance.editadoExitosamente
            .subscribe({
                next: () => {
                    this.alert.success("Unidad de medida editada con éxito.");
                    this.tabla.filters(this.filtroBusqueda);
                },
            });
    }

    /**
     * Esta funcion elimina un item
     * @param event 
     */
    deleteUnidadDeMedida(event) {
        this.confirm.open({
            title: "Eliminar Unidad de medida " + event.params.data.descripcion,
            message: "¿Desea eliminar la Unidad de medida: " + event.params.data.descripcion + "?",
            dismissible: false,
            actions: {
                confirm: {
                    show: true,
                    color: 'warn',
                    label: 'Si',
                },
                cancel: {
                    show: true,
                    label: 'No',
                },
            },
        })
            .afterClosed()
            .subscribe(res => {
                if (res === 'confirmed') {
                    this._unidadesDeMedidaService.deleteUnidadDeMedida(event.params.data.idunidadmedida).subscribe({
                        next: (data) => {
                            // Manejar la respuesta exitosa
                            this.alert.success("Unidad de medida eliminada con éxito.");
                            if (res) {
                                this.tabla.renderTabla()
                            }
                        },
                        error: (error) => {
                            // Manejar el error
                            console.error(error);
                            this.alert.warning("Esta Unidad de medida no puede eliminarse. Tiene elementos asociados.");
                        }
                    });
                }
            });
    }

    ngOnDestroy(): void {
        this._searchService.unsubscribe()
    }
}