import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'environments/environment';
import { Observable, Subject } from 'rxjs';
import { IUnidadDeMedida, Response } from '../models/unidad-de-medida';
import { AuthService } from '@core/auth/auth.service';

@Injectable()
export class UnidadesDeMedidaService {
    constructor(private http: HttpClient, private _authService: AuthService) { }
    /**
     * Funcion en el servicio
     * - Esta funcion retorna los unidades de medidas.
     * @returns Unidades de medidas 
     */
    getUnidadesDeMedida(filter?: any): Observable<Response[]> {
        let f = '?';
        f += filter == 'desc' ? 'sort=-descripcion' : 'sort=descripcion';
        var subject = new Subject<any>();

        this.http.get<Response[]>(`${environment.stock.unidad_medidas}${f}`)
            .subscribe((resp: any) => {
                this._authService.accessToken = resp.token,
                    subject.next({
                        'list': resp.result,
                        // 'totalPages': resp.headers.get('x-pagination-page-count'),
                        // 'totalCount': resp.headers.get('x-pagination-total-count')
                    })
            });
        return subject.asObservable();
    }
    /**
     * Funcion en el Servicio 
     * @param unidad_medida 
     * @returns Una unidad de medida
    */
    saveUnidadDeMedida(unidad_medida: IUnidadDeMedida): Observable<IUnidadDeMedida> {
        return this.http.post<IUnidadDeMedida>(`${environment.stock.unidad_medidas}`, unidad_medida);
    }
    /**
     * Funcion en el servicio
     * @param unidad_medida 
     * @returns Una unidad de medida actualizada
    */
    updateUnidadDeMedida(unidad_medida: IUnidadDeMedida): Observable<IUnidadDeMedida> {
        return this.http.put<IUnidadDeMedida>(`${environment.stock.unidad_medidas}` + '/' + unidad_medida.idunidadmedida, unidad_medida);
    }
    /**
     * Funcion en el servicio
     * @param idunidadmedida 
     * @returns Elimina la unidad de medida
    */
    deleteUnidadDeMedida(idunidadmedida: number): Observable<IUnidadDeMedida> {
        return this.http.delete<IUnidadDeMedida>(environment.stock.unidad_medidas + '/' + idunidadmedida);
    }

}
